  /* Styla result-container */
#result {
    font-size: 24px;
    text-align: center;
    margin-bottom: 20px;
  }
 
  /* Styla highscore-container */
  #highscore-container {
    text-align: center;
    background-color: aqua;
    padding: 10px;
    border-radius: 5px;
    margin-bottom: 20px;
  }
 
  /* Styla rubriken "Highscore" */
  #highscore-container h2 {
    font-size: 20px;
    margin-bottom: 10px;
  }
 
  /* Styla highscore-listan */
  #highscore-list {
    list-style-type: decimal;
    padding-left: 0;
  }
 
